@import "partials/variables";
@import "partials/mixins";
@import "partials/functions";

* {
    // Include border and padding in total dimensions.
    box-sizing: border-box;
    // Removes auto margins added by browsers.
    margin: 0;
    // Fix for fonts appearing at the wrong weight in Chrome.
    -webkit-font-smoothing: antialiased;
}

/* Change the autofill to any color */
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active
{
 -webkit-box-shadow: 0 0 0 30px white inset !important;
}

.container {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  
.btn {
  border-radius: 999em;
  border: 1px solid $brand-orange;
  padding: 18px 50px 15px 50px;
  width: 280px;
  text-align: center;
  font-family: 'Khula';
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.3rem;
  letter-spacing: 1.19pt;
  text-transform: uppercase;
  background-color: $brand-orange;
  color: $brand-yellow;
  transition: .3s ease all;
  cursor: pointer;
  &:hover {
      background-color: $white;
      color: tint($brand-darkBlue, 25%);
  }
  &:focus {
      border: 1px solid $brand-darkBlue;
      background-color: $brand-darkBlue;
      color: $white;
  }
}

h1 {
  font-family: 'Yellowtail', cursive;
  color: $brand-darkBlue;
}

p {
  font-family: "Khula", sans-serif;
  color: $brand-darkBlue;
}

label {
  font-family: "Khula", sans-serif;
}